// 함수형 사용 전 위쪽에 만들어두기
@mixin function-mini() {
    background: #eeeeee;
    padding:20px;
    border-radius: 5px;
    max-width:500px;
    width:100%;
    margin:auto;
}

.alert-sold-out {
 @include function-mini()
}

.alert-sold-out-yellow {
    @extend .alert-sold-out;
    background: hsl(44, 100%, 50%);
}

.alert-sold-out p {
    margin-bottom: 0;

}
