.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.jumbotron{
  padding:2rem 1rem;
  margin-bottom:2rem;
  background-color:#e9ecef;
  background-image: url('./res/anime-hayao-miyazaki-birthday-wallpapers-studio-ghibli-971.jpeg');
  border-radius:.3rem;
  color: azure;
}

.jumbotron-fluid{
  padding-right:0;
  padding-left:0;
  border-radius:0;
}

.jumbotron h1 {
  color: #ffffffad;
}

.loading-view {
    background: #ffbaba;
    padding:20px;
    border-radius: 5px;
    max-width:500px;
    width:100%;
    margin:auto;
}

.loading-view p {
  margin-bottom: 0;
}



